import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import ExtInfoList from "@/microApps/root/components/modelChildren/ExtInfoList.vue";
import VideoPlayer from "@/microApps/root/components/modelChildren/VideoPlayer.vue";

@Component({
  components: { VideoPlayer, ExtInfoList }
})
export default class DeviceModelChild extends Vue {
  @Prop({ type: Object, required: true })
  detailData!: any;

  markerExtInfoVO: any[] = [];
  isCamera: boolean = false;
  videoSource: string = "";

  @Watch("detailData", { immediate: true, deep: true })
  handleFunction(val: any) {
    this.isCamera = false;
    this.videoSource = "";
    if (val && val.extInfoVO) {
      if (val.extInfoVO.filter((i: any) => i.name === "摄像头" && i.value === "是").length) {
        this.videoSource = val.extInfoVO.filter((i: any) => i.name === "视频流地址")[0].value;
        this.isCamera = true;
        this.markerExtInfoVO = [];
      } else {
        this.markerExtInfoVO = val.extInfoVO.filter((i: any) => i.value && i.value !== " " && i.value !== "null");
      }
    }
  }
}
