import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import url from "../api";
import BuildingModelChild from "@/microApps/root/components/modelChildren/BuildingModelChild.vue";
import DeviceModelChild from "@/microApps/root/components/modelChildren/DeviceModelChild.vue";

@Component({
  components: { BuildingModelChild, DeviceModelChild }
})
export default class MarkerInfoModel extends Vue {
  @Prop({ type: String, required: true })
  type!: string;
  @Prop({ type: String, required: true })
  id!: string;
  @Prop({ type: String, required: true })
  name!: string;
  @Prop({ type: Boolean, required: false, default: false })
  isDirective!: boolean;

  markerDetail: any = {};
  witchModel: string = "DeviceModelChild";
  isLoading: boolean = true;
  active: number = 0;
  timer: any;
  warnings: any[] = [];

  //
  hasAcquiredMarkers: { [key: string]: any } = {};

  closeModel() {
    this.$emit("close");
  }

  get showDirective() {
    return this.isDirective ? "directive" : "";
  }

  async getMarkerInfo(id: string) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get((url.getDetail as any)[this.type] + id);
      if (status) {
        this.markerDetail = data;
        this.$set(this.hasAcquiredMarkers, id, data);
        if (data.deviceStatusRelations && data.deviceStatusRelations.length) {
          this.warnings = data.deviceStatusRelations;
        }
      } else {
        this.$message.error(message);
        this.markerDetail = null;
        this.$set(this.hasAcquiredMarkers, id, null);
      }
      this.isLoading = false;
    } catch (e) {
      return;
    }
  }

  @Watch("id", { immediate: true, deep: true })
  changeMarkerInfo(val: string) {
    this.isLoading = true;
    // 保存已请求数据（取消该方法， 默认每次请求）
    // if (this.hasAcquiredMarkers[val]) {
    //   this.markerDetail = this.hasAcquiredMarkers[val];
    //   if (this.hasAcquiredMarkers[val].deviceStatusRelations && this.hasAcquiredMarkers[val].deviceStatusRelations.length) {
    //     this.warnings = this.hasAcquiredMarkers[val].deviceStatusRelations;
    //   } else {
    //     this.warnings = [];
    //   }
    //   this.isLoading = false;
    // } else {
    this.warnings = [];
    this.getMarkerInfo(val);
    // }
  }

  @Watch("type", { immediate: true })
  changeModel(val: string) {
    switch (val) {
      case "building":
        this.witchModel = "BuildingModelChild";
        break;
      case "car":
        this.witchModel = "CarModelChild";
        break;
      case "person":
        this.witchModel = "PersonModelChild";
        break;
      case "device":
        this.witchModel = "DeviceModelChild";
        break;
      default:
        this.witchModel = "DeviceModelChild";
    }
  }

  @Watch("warnings", { immediate: true, deep: true })
  changeWarning(val: any) {
    if (val && val.length) {
      this.active = 0;
      this.timer = setInterval(() => {
        if (this.active < val.length - 1) {
          this.active++;
        } else {
          this.active = 0;
        }
      }, 2000);
    } else {
      this.active = 0;
      if (this.timer) clearInterval(this.timer);
    }
  }

  beforeDestroy() {
    clearInterval(this.timer);
  }
}
