<template>
  <video v-if="reset && source" :id="videoId" controls ref="video" poster="" playsInline webkit-playsinline autoplay>
    <source ref="source" :src="source" type="video/mp4" id="mySource3" />
  </video>
</template>

<script>
let videoId = 0;
// export
export default {
  name: "video-player",
  props: {
    source: { type: String }
  },
  data() {
    return {
      reset: true,
      videoId: videoId++
    };
  },
  watch: {
    source() {
      this.reset = false;
      this.$nextTick(() => {
        this.reset = true;
        this.$nextTick(() => {
          this.play();
        });
      });
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.play();
    });
  },
  beforeDestroy() {
    this.player.stop();
    setTimeout(() => {
      this.player.stop();
    }, 2000);
  },
  methods: {
    play() {
      this.player = new EZUIPlayer(this.videoId + "");
      this.player.on("error", function() {
        console.log("error");
      });
      this.player.on("play", function() {
        console.log("play");
      });
      this.player.on("pause", function() {
        console.log("pause");
      });
      this.player.play();
    }
  }
};
</script>

<style lang="scss" scoped>
video {
  display: block;
  width: 100%;
  max-height: 320px;
  height: 100%;
}
</style>
