import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import ExtInfoList from "@/microApps/root/components/modelChildren/ExtInfoList.vue";

@Component({
  components: { ExtInfoList }
})
export default class BuildingModelChild extends Vue {
  @Prop({ type: Object, required: true })
  detailData!: any;

  markerExtInfoVO: any[] = [];

  @Watch("detailData", { immediate: true, deep: true })
  handleFunction(val: any) {
    if (val) {
      !!val.extInfoVO && (this.markerExtInfoVO = val.extInfoVO.filter((i: any) => i.value && i.value !== " " && i.value !== "null"));
    } else {
      this.markerExtInfoVO = [];
    }
  }
}
