 * @Date: -
 **/

import { Vue, Component, Prop, Watch, Model } from "vue-property-decorator";
import url from "../api";
import util from "@/utils/utils";
const AMap: any = (window as any).AMap;

@Component({})
export default class ToolsList extends Vue {
  @Model("input")
  toolPopover!: boolean;

  popoverStatus: string = "";
  toolBarVisible: boolean = true;
  orgId: string = "";
  // toolbar 对应图标'
  systemToolIcon: { [key: string]: { [key: string]: string } } = {
    systemPerson: {
      name: "人员",
      icon: "icon-renyuanx",
      type: "person"
    },
    systemDevice: {
      name: "设备",
      icon: "icon-shebeix",
      type: "device"
    },
    systemCar: {
      name: "车辆",
      icon: "icon-cheliangx",
      type: "car"
    },
    systemBuilding: {
      name: "建筑",
      icon: "icon-jianzhux",
      type: "building"
    }
  };
  toolsIcon: { [key: string]: { [key: string]: string } } = {
    range: {
      name: "范围",
      icon: "icon-fanweix"
    },
    distance: {
      name: "测距",
      icon: "icon-cejux"
    },
    viewMode: {
      name: "二/三维",
      icon: "icon-ersanweiqiehuanx"
    },
    satellite: {
      name: "卫星",
      icon: "icon-weixingx"
    },
    road: {
      name: "路况",
      icon: "icon-lukuangx"
    }
  };
  // toolbar 各类型数据列表
  systemToolsList: any[] = [];
  userToolsList: any[] = [];
  toolsList: any[] = [];
  // 保存toolbar各个类型对应数据
  systemToolData: { [key: string]: any[] } = {
    systemPerson: [],
    systemDevice: [],
    systemCar: [],
    systemBuilding: []
  };
  userToolData: { [key: string]: any[] } = {};
  // 每个下拉框按钮的点击事件记录
  clickStatusEvents: { [key: string]: boolean } = {};
  // 每个大类按钮点击事件记录
  toolClickStatusEvents: { [key: string]: any[] } = {};
  // 保存所有生成的marker实例
  mapMarkers: { [key: string]: any[] } = {};
  // 保存地图上所有显示已显示的地图标记
  onMapMarkers: { name: string; children: any[]; [key: string]: any }[] = [];

  @Watch("toolPopover", { immediate: false })
  changePopoverStatus(val: boolean) {
    if (!val) this.popoverStatus = "";
  }

  @Watch("popoverStatus", { immediate: false })
  returnStatus(val: string) {
    this.$emit("input", !!val);
  }

  created() {
    this.orgId = JSON.parse(localStorage.getItem("orgIds") as string)[0];
    this.getUserToolsList();
    this.getSystemToolsList();
  }

  /**
   * 树结构扁平化
   * @param arr 原数组
   * @param targetArr 目标数组
   * @param level 节点层级
   */
  platen(arr: any[], targetArr: any[], level: number = 0) {
    arr.forEach((i: any) => {
      targetArr.push({
        ...i,
        level: level
      });
      if (i.children && i.children.length) {
        this.platen(i.children, targetArr, level + 1);
      }
    });
  }
  changeToolsStatus() {
    this.popoverStatus = this.popoverStatus === "tools" ? "" : "tools";
  }

  // 用户自定义工具栏，以及每个工具对应绑定类型列表
  async getUserToolsList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(url.toolBar.getUserTools + "?orgId=" + this.orgId);
      if (status) {
        this.userToolsList = data;
        this.userToolsList.forEach((i: any) => {
          this.userToolData[i.id] = i.mapMenuCustomToolbarDetailVos;
        });
        // 遍历自定义数组,对默认显示项目请求并显示对应数据
        this.userToolsList
          .filter((i: any) => i.isShow)
          .forEach((n: any) => {
            if (n.mapMenuCustomToolbarDetailVos && n.mapMenuCustomToolbarDetailVos.length) {
              n.mapMenuCustomToolbarDetailVos.forEach((m: any) => {
                this.getMapMarkers(n.typeId, m, n.id);
              });
            }
          });
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }

  // 系统自定义工具栏
  async getSystemToolsList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(url.toolBar.getSystemTools + "?orgId=" + this.orgId);
      if (status) {
        this.systemToolsList = JSON.parse(data).checkedSystemData;
        this.toolsList = JSON.parse(data).checkedSystemTools;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }

  // 用户定义工具栏获取数据
  async getUserToolData(item: any) {
    if (this.userToolData[item.id] && this.userToolData[item.id].length) {
      // 如果绑定的类型大于1种，则显示下拉框
      this.popoverStatus = this.popoverStatus === item.name ? "" : item.name;
    } else {
      this.popoverStatus = "";
      this.$message.error("该按钮暂未绑定具体类型，请重新设置");
    }
    // else if (this.userToolData[item.id] && this.userToolData[item.id].length) {
    //   // 否则直接调用获取类型对应的所有点标记坐标
    //   this.getMapMarkers(item.typeId, this.userToolData[item.id][0].typeDetailId, this.userToolData[item.id][0].icon);
    // }
  }

  // 系统预设工具栏获取数据
  async getSystemToolData(type: string) {
    this.popoverStatus = this.popoverStatus === type ? "" : type;
    if (!this.systemToolData[type] || !this.systemToolData[type].length) {
      // 没有数据再获取
      try {
        let {
          data: { data, status, message }
        } = await this.$http.get(`${url.findTypeTree}/${type.substring(6).toLowerCase()}`);
        if (status) {
          if (!this.systemToolData[type]) this.$set(this.systemToolData, type, []);
          this.platen(data, this.systemToolData[type], 1);
        } else {
          this.$message.error(message);
        }
      } catch (e) {
        return;
      }
    }
  }

  // 系统工具点击事件
  toolsClick(type: string) {
    if (this.clickStatusEvents["tool" + type]) {
      this.clickStatusEvents["tool" + type] = false; // 下拉框项目状态切换
      if (this.toolClickStatusEvents["toolbar"])
        this.toolClickStatusEvents["toolbar"] = this.toolClickStatusEvents["toolbar"].filter((i: string) => i !== type);
    } else {
      this.clickStatusEvents["tool" + type] = true; // 下拉框项目状态切换
      if (!this.toolClickStatusEvents["toolbar"]) {
        this.$set(this.toolClickStatusEvents, "toolbar", []);
      }
      this.toolClickStatusEvents["toolbar"].push(type);
    }
    this.popoverStatus = ""; // 隐藏下拉框，避免样式无法修改
    this.$emit("toolClick", type);
  }

  //
  screenFull() {
    this.$emit("click");
    if (this.clickStatusEvents["tool-fullscreen"]) {
      this.clickStatusEvents["tool-fullscreen"] = false;
    } else {
      this.$set(this.clickStatusEvents, "tool-fullscreen", true);
    }
  }

  /**
   * 根据具体类型id与大类标识查询数据，并保存对应点击事件状态
   * @param type 大类类型
   * @param nodeItem 具体小类型详细信息
   * @param key 工具栏点击事件状态map的key
   */
  async getMapMarkers(type: string, nodeItem: any, key: string) {
    let typeId: string = nodeItem.typeDetailId ? nodeItem.typeDetailId : nodeItem.id;
    let icon: string = nodeItem.icon;
    let typeName: string = nodeItem.name;
    // 判断工具栏对应是否有值，没有则新建属性，否则样式不改变
    if (!this.toolClickStatusEvents[key]) this.$set(this.toolClickStatusEvents, key, []);
    // 隐藏下拉框，重置样式
    // this.popoverStatus = "";
    // 已有数据且click状态为true，隐藏点标记
    if (this.mapMarkers[typeId] && this.clickStatusEvents[key + typeId]) {
      this.$emit("hide", this.mapMarkers[typeId], typeId);
      this.clickStatusEvents[key + typeId] = false;
      // 避免数组长度异常报错
      if (this.toolClickStatusEvents[key].length <= 1) {
        this.toolClickStatusEvents[key] = [];
      } else {
        this.toolClickStatusEvents[key].length = this.toolClickStatusEvents[key].length - 1;
      }
    } else {
      if (type !== "person" && type !== "car" && this.mapMarkers[typeId] && this.mapMarkers[typeId].length) {
        // 已有数据且click状态为false，显示点标记
        // this.clickStatusEvents[key + typeId] = true;
        this.$set(this.clickStatusEvents, key + typeId, true);
        this.$emit("show", this.mapMarkers[typeId], this.onMapMarkers, typeId);
        this.toolClickStatusEvents[key].push(key);
        return;
      } else {
        // "没有数据"或者"请求类型为人或者车"的情况下，查询对应数据
        try {
          let {
            data: { data, status, message }
          } = await this.$http.get(url.getMarkerData, {
            params: {
              typeId: type,
              typeDataId: typeId
            }
          });
          if (status) {
            // 存在icon的话再设置icon，否则显示默认icon
            let defaultIcon: any = "";
            switch (type) {
              case "person":
                defaultIcon = require("../styles/defaultIcon/person.svg");
                break;
              case "car":
                defaultIcon = require("../styles/defaultIcon/car.svg");
                break;
              case "building":
                defaultIcon = require("../styles/defaultIcon/building.svg");
                break;
              default:
                defaultIcon = require("../styles/defaultIcon/device.svg");
            }
            let markerIcon: any = new AMap.Icon({
              size: new AMap.Size(30, 30),
              image: icon ? icon : defaultIcon,
              imageSize: new AMap.Size(30, 30)
            });
            // 清空当前对应点数据
            this.mapMarkers[typeId] = [];
            // 保存当前类型的所有“具有经纬度”的点
            let markersOfThisType: any[] = [];
            // 查询数据不为空的情况下实例化对应点标记，并保存到mapMarkers中
            if (data.mapDataVos && data.mapDataVos.length) {
              data.mapDataVos
                .map((n: any) => {
                  return {
                    ...n,
                    id: n.dataId
                  };
                })
                .forEach((i: any, key: number) => {
                  // 存入children
                  markersOfThisType.push({
                    ...i,
                    level: "child"
                  });
                  // 有具体经纬度信息则实例化
                  if (i.lng && i.lat) {
                    this.mapMarkers[typeId].push(
                      new AMap.Marker({
                        position: [i.lng, i.lat],
                        icon: markerIcon,
                        offset: new AMap.Pixel(-15, -30),
                        zIndex: 120
                      }).on("click", () => {
                        if (type !== "car" && type !== "person") {
                          this.$emit("markerClick", type, i, key);
                        }
                      })
                    );
                  }
                });
              // 为避免人/车重复添加， 排除已有类型id的数据，将所有点保存进对应类型中
              if (!this.onMapMarkers.filter((o: any) => o.id === typeId).length) {
                this.onMapMarkers.push({
                  name: typeName,
                  id: typeId,
                  children: markersOfThisType
                });
              }
              // 返回各种状态
              this.$set(this.clickStatusEvents, key + typeId, true);
              this.$emit("show", this.mapMarkers[typeId], this.onMapMarkers, typeId);
              this.toolClickStatusEvents[key].push(key);
            } else {
              // 否则提示未查询到数据，click状态置为false
              this.$message.warning("类型“" + nodeItem.name + "”未查询到相关数据");
              this.clickStatusEvents[key + typeId] = false;
            }
          } else {
            this.$message.error("类型“" + nodeItem.name + "”数据查询失败：" + message);
          }
        } catch (e) {
          return;
        }
      }
    }
  }
}
