import { Vue, Component, Prop, Watch, Model } from "vue-property-decorator";

@Component({})
export default class SearchBox extends Vue {
  @Prop({ type: Array, required: true })
  markerList!: any[];

  filterText: string = "";
  searchProps: any = {
    children: "children",
    label: "name"
  };

  @Model("input")
  elementStatus!: boolean;

  @Watch("filterText", { immediate: false })
  doFilter(val: string) {
    (this.$refs["searchTree"] as any).filter(val);
  }

  @Watch("markerList", { immediate: false })
  returnStatus(val: any[]) {
    if (!val.length) this.$emit("change", false);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.searchProps.label].indexOf(value) !== -1;
  }

  changeStatus() {
    this.$emit("change", !this.elementStatus);
  }

  handleNodeClick(node: any) {
    this.$emit("node-click", node);
  }
}
