import { Vue, Component, Watch } from "vue-property-decorator";
import ToolsList from "@/microApps/root/components/ToolsList.vue";
import MapMixins from "@/microApps/root/MapMixins";
import url from "../api";
import ChartsList from "@/microApps/root/components/ChartsList.vue";
import SearchBox from "@/microApps/root/components/SearchBox.vue";
import MarkerInfoModel from "@/microApps/root/components/MarkerInfoModel.vue";
import api from "@/microApps/authority/api";

const AMap: any = (window as any).AMap;

@Component({
  components: { MarkerInfoModel, SearchBox, ChartsList, ToolsList }
})
export default class HomePage extends Vue {
  fullscreen: boolean = false; // 全屏状态
  searchBoxStatus: boolean = false; // 右侧搜索框状态
  toolPopovers: boolean = false; // 工具栏下拉框状态
  showCharts: boolean = true; // 图表显示状态
  showMarkerInfo: boolean = false; // 图表显示状态
  orgId: string = "";
  leftChartsList: any[] = [];
  rightChartsList: any[] = [];

  map: any;
  mapStyle: any = {};
  trafficLayer: any = null;
  satelliteLayer: any = null;
  mouseTool: any = null;
  // 选中的工具
  selectedToolbar: { [key: string]: any } = {};
  // 绑定右侧列表的点标记（地图上显示出来的点）
  markersList: any[] = [];
  // filterList
  markersListFilter: string[] = [];
  //
  markerId: string = "";
  markerType: string = "";
  markerName: string = "";
  mapEvent: any;
  hasDirective: boolean = false;

  timer: any = null;
  eventMarkers: any[] = [];

  // 初始化地图
  initMap() {
    this.map = new (window as any).AMap.Map("home-page-map", {
      center: [106.5, 29.5],
      resizeEnable: true,
      viewMode: "3D",
      zoom: 10,
      pitchEnable: false,
      rotateEnable: false
    })
      .on("click", () => {
        // 隐藏工具栏下拉框
        this.toolPopovers = false;
      })
      .on("movestart", () => {
        this.toolPopovers = false;
        this.hasDirective = false;
      });
    AMap.plugin(["AMap.MouseTool"]);
    this.trafficLayer = new AMap.TileLayer.Traffic({
      zIndex: 100
    });
    this.satelliteLayer = new AMap.TileLayer.Satellite({
      zIndex: 99
    });
  }

  // 工具点击事件
  toolBarClick(type: string) {
    if (this.selectedToolbar[type]) {
      switch (type) {
        case "range":
          break;
        case "viewMode":
          this.map.setPitch(0);
          this.map.setStatus({
            pitchEnable: false,
            rotateEnable: false // 地图是否可旋转，3D视图默认为true，2D视图默认false
          });
          break;
        case "distance":
          this.mouseTool.close();
          this.mouseTool = null;
          break;
        case "satellite":
          this.satelliteLayer.hide();
          break;
        case "road":
          this.trafficLayer.hide();
          break;
        default:
          return;
      }
      this.selectedToolbar[type] = false;
    } else {
      switch (type) {
        case "range":
          break;
        case "viewMode":
          this.map.setStatus({
            pitchEnable: true,
            rotateEnable: true // 地图是否可旋转，3D视图默认为true，2D视图默认false
          });
          break;
        case "distance":
          this.mouseTool = new AMap.MouseTool(this.map);
          this.mouseTool.rule({
            startMarkerOptions: {
              //可缺省
              icon: new AMap.Icon({
                size: new AMap.Size(19, 31), //图标大小
                imageSize: new AMap.Size(19, 31),
                image: "https://webapi.amap.com/theme/v1.3/markers/b/start.png"
              })
            },
            endMarkerOptions: {
              //可缺省
              icon: new AMap.Icon({
                size: new AMap.Size(19, 31), //图标大小
                imageSize: new AMap.Size(19, 31),
                image: "https://webapi.amap.com/theme/v1.3/markers/b/end.png"
              }),
              offset: new AMap.Pixel(-9, -31)
            },
            midMarkerOptions: {
              //可缺省
              icon: new AMap.Icon({
                size: new AMap.Size(19, 31), //图标大小
                imageSize: new AMap.Size(19, 31),
                image: "https://webapi.amap.com/theme/v1.3/markers/b/mid.png"
              }),
              offset: new AMap.Pixel(-9, -31)
            },
            lineOptions: {
              //可缺省
              strokeStyle: "solid",
              strokeColor: "#FF33FF",
              strokeOpacity: 1,
              strokeWeight: 2
            }
            //同 RangingTool 的 自定义 设置，缺省为默认样式
          });
          break;
        case "satellite":
          this.satelliteLayer.setMap(this.map);
          this.satelliteLayer.show();
          break;
        case "road":
          this.trafficLayer.setMap(this.map);
          this.trafficLayer.show();
          break;
        default:
          return;
      }
      this.selectedToolbar[type] = true;
    }
  }

  // 获取地图样式
  async getMapStyle() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(url.mapStyle + "?orgId=" + this.orgId);
      if (status) {
        this.mapStyle = data;
      }
    } catch (e) {
      return;
    }
  }
  // 获取两侧图表
  async getChartsList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${url.chartsList}?orgId=${this.orgId}`);
      if (status) {
        this.leftChartsList = data.filter((i: any) => i.position === 0);
        this.rightChartsList = data.filter((i: any) => i.position === 1);
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }

  /**
   * 显示点标记,并插入右侧搜索列表
   * @param markers 当前选中类型对应的所有点标记
   * @param markersOnMap 地图上所有已经实例化的点标记对应的点详情数组
   * @param typeId 当前选中类型id
   */
  showMarkerOnMap(markers: any[], markersOnMap: any[], typeId: string) {
    this.map.add(markers);
    this.map.setFitView();
    this.searchBoxStatus = true;
    if (!this.markersListFilter.includes(typeId)) {
      this.markersListFilter.push(typeId);
    }
    this.markersList = markersOnMap.filter((i: any) => this.markersListFilter.includes(i.id));
  }

  /**
   * 隐藏点标记,并移除右侧对应点标记
   * @param markers 当前选中类型对应的所有点标记
   * @param typeId 当前选中类型id
   */
  hideMarkerOnMap(markers: any[], typeId: string) {
    this.map.remove(markers);
    if (this.markersListFilter.includes(typeId)) {
      this.markersListFilter.splice(this.markersListFilter.indexOf(typeId), 1);
    }
    this.markersList = this.markersList.filter((i: any) => this.markersListFilter.includes(i.id));
  }

  /**
   * 点标记点击事件
   * @param type 大类类型（car，building，person，device）
   * @param marker 单个点标记
   * @param key
   */
  markerClickEvent(type: string, marker: any, key?: number) {
    // 隐藏工具栏下拉框
    this.toolPopovers = false;
    this.map.setCenter([marker.lng, marker.lat]);
    this.markerName = marker.name;
    this.markerId = marker.dataId;
    this.markerType = type;
    this.showMarkerInfo = true;
    this.hasDirective = true;
  }

  // 查询列表显示状态
  changeSearchBoxStatus(val: boolean) {
    // 隐藏工具栏下拉框
    this.toolPopovers = false;
    this.searchBoxStatus = val;
    this.showCharts = !val;
  }

  // 搜索列表点击事件
  searchMarker(node: any) {
    // 隐藏工具栏下拉框
    this.toolPopovers = false;
    if (node.level === "child") {
      if (node.lat && node.lng) {
        this.map.setCenter([node.lng, node.lat]);
      } else {
        this.$message.warning("该节点未绑定位置信息");
      }
    } else {
      // this.$message.info("请选择具体节点");
    }
  }

  // 定时查询报警设备
  async getAllEvents() {
    if (this.timer) clearTimeout(this.timer);
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(url.getAllEvent);
      if (status) {
        this.map.remove(this.eventMarkers);
        this.eventMarkers = [];
        if (data && data.length) {
          data.forEach((i: any) => {
            if (i.lng && i.lat) {
              this.addEventMarker(i);
            }
          });
        }
      }
    } finally {
      this.timer = setTimeout(() => {
        this.getAllEvents();
      }, 10000);
    }
  }
  // 获取当前团队信息
  async getTenantInfo() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getTenantInfo);
    if (status) {
      // this.tenantInfo.name = data.name;
      localStorage.setItem("tenantInfo", JSON.stringify(data));
    } else {
      this.$message.error(message);
    }
  }

  // 事件点位
  addEventMarker(node: any) {
    let eventMarker: any = new AMap.Marker({
      map: this.map,
      position: [node.lng, node.lat],
      offset: new AMap.Pixel(-15, -15)
    }).on("click", () => {
      let marker: any = {
        name: node.deviceName,
        lat: node.lat,
        lng: node.lng,
        dataId: node.deviceId,
        warningValue: node.deviceStatusVos
      };
      this.markerClickEvent("device", marker);
    });
    eventMarker.setContent(
      `<div class="warn-point" style="background: radial-gradient(at center, ${node.deviceStatusVos[0].styleColor || "#aa0000"}, ${
        node.deviceStatusVos[0].styleColor ? node.deviceStatusVos[0].styleColor + "01" : "#aa000011"
      });"></div>`
    );
    this.map.add(eventMarker);
    this.eventMarkers.push(eventMarker);
  }

  created() {
    this.orgId = JSON.parse(localStorage.getItem("orgIds") as string)[0];
    this.getChartsList();
    this.getTenantInfo();
  }

  mounted() {
    this.getMapStyle().finally(() => {
      this.$nextTick(() => {
        this.initMap();
        if (this.mapStyle && this.mapStyle.style && this.map) {
          this.map.setMapStyle(`amap://styles/${this.mapStyle.style}`);
        }
        if (this.mapStyle && this.mapStyle.mapCenter && this.map) {
          this.map.setCenter(this.mapStyle.mapCenter.split(","));
        }
        if (this.mapStyle && this.mapStyle.mapScale && this.map) {
          this.map.setZoom(this.mapStyle.mapScale);
        }
      });
      this.getAllEvents();
    });
  }

  beforeDestroy() {
    this.map.destroy();
    this.map = null;
  }

  @Watch("fullscreen", { immediate: false })
  handleChangeScreen(val: boolean) {
    let element: any = this.$refs["homePageMap"];
    const document: any = (window as any).document;
    // 判断是否已经是全屏
    // 如果是全屏，退出
    if (val) {
      // 否则，进入全屏
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.webkitRequestFullScreen) {
        element.webkitRequestFullScreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.msRequestFullscreen) {
        // IE11
        element.msRequestFullscreen();
      }
      console.log("已全屏！");
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
      console.log("已还原！");
    }
  }
}
